.feed{
    flex: 0.6;
    margin:0 20px;
}

.feed__inputContainer{
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.feed__input{
    border: 1px solid lightgray;
    display: flex;
    border-radius: 20px;
    padding: 10px;
    padding-left: 20px;
    color: gray;
}

.feed__input >form{
    display: flex;
    width: 100%;
}

.feed__input > form > input{
    flex: 1;
    border: none;
    outline-width: 0;
    margin-left: 10px;
    font-weight: 600;
}

.feed__input > form> button{
    display: none;
}

.feed__inputOptions{
    display: flex;
    justify-content: space-evenly;
    padding: 10px;
}
